@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;600&display=swap);
* {
  margin: 0px;
  padding: 0px;
}

body {
  background-color: #25705A;
}

input {
  margin-left: 10px;
  outline: none;
}

button {
  background-color: transparent;
  color: #fff;
  border: 0px;
  outline: none;
  cursor: pointer;
  font-size: 1.2em;
}

button:hover {
  transform: scale(1.1);
}
.App {
  max-width: 50%;
  margin: 0px auto;
  font-family: 'Playfair Display', serif;
  font-size: 15px;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.404);
}
.AppHeader {
  background-color: #AA5939;
}

.AppHeader > p {
  padding: 5px 0px 5px 10px;
}
.AppBody {
  height: 230px;
  background-color: #fff;
  overflow: auto;
}
.AppTodoList {
  display: flex;
  flex-direction: column;
  max-width: 80%;
  margin: 10px auto;
}

.AppTodoList .AppTodoItem:not(:last-child) {
  margin-bottom: 5px;
}
.AppTodoItem {
  display: flex;
  justify-content: space-between;
  padding: 5px 7px;
  background-color: #551800c4;
  border-radius: 5px;
  font-size: 0.8em;
}

.ItemDelete {
  color: rgb(240, 97, 97);
}
.AppAddItem {
  display: flex;
  justify-content: center;
  margin: 10px 0px;
}

.AddItemButton {
  margin-left: 10px;
  font-size: 1em;
  transform: scale(1.2);
  cursor: pointer;
}

.AddItemButton:active {
  color: rgb(42, 197, 42);
}
.AppFooter {
  display: flex;
  justify-content: space-between;
  padding: 5px 10px;
  background-color: #AA5939;
  font-size: 0.7em;
}
