* {
  margin: 0px;
  padding: 0px;
}

body {
  background-color: #25705A;
}

input {
  margin-left: 10px;
  outline: none;
}

button {
  background-color: transparent;
  color: #fff;
  border: 0px;
  outline: none;
  cursor: pointer;
  font-size: 1.2em;
}

button:hover {
  transform: scale(1.1);
}